.about-me {
    position: absolute;
    height: 10vh;
    top: 0;
    width: 100%;
}

.about-me-container {
    display: flex;
}

.about-me-left {

    width: 30%;
    background-color: #384449;
    height: 100vh;

}

.about-me-img {
    position: absolute;
    left: 22%;
    top: 20rem;
    // max-width: 40rem;
    width: 27%;
    height: auto;
    z-index: 2;
}

.about-me-right {
    padding-top: 10rem;
    width: 70%;
    //background-color: 575A5E
}

.credentials {
    margin-top: 60rem;
    margin-right: 30%;
    color: white;

    h3 {
        font-size: 1.7rem;
    }

    h4 {
        font-size: 1.5rem;
        font-weight: 400;
        padding-bottom: 2rem;
    }
}

.cv-buttons {
    background-color: black;

    display: inline-block;
    margin: 2rem auto 0 auto;

    h3 {
        padding-bottom: 1rem;
    }

    button:first-of-type {
        padding-right: .4rem;
    }

}

.about-me-story {
    width: 40%;
    margin-top: 15%;
    margin-left: 30%;

    h1,
    h2 {
        font-size: 4rem;
        color: #69887E
    }

    h3 {
        color: #575A5E
    }

    p {
        padding: 5rem 0;
        font-size: 1.6rem;
        text-align: justify;
    }

}

@media screen and(max-width:1024px) {
    .about-me-img {
        position: initial;
        margin-top: 6rem;
        width: 100%;
        height: auto;

    }

    .credentials {
        margin: 2rem auto;

        color: white;

        h3 {
            font-size: 1.4rem;
        }

        h4 {
            font-size: 1.3rem;
            font-weight: 400;
            padding-bottom: 1rem;
        }
    }
.about-me-right{
    padding-top: 2rem;
}
    .about-me-story {
        width: 95%;
       margin: 2rem;
        padding: 3rem;

        h1,
        h2 {
            font-size: 4rem;
            color: #69887E
        }

        h3 {
            color: #575A5E
        }

        p {
            padding: 5rem 0;
            font-size: 1.6rem;
            text-align: justify;
        }

    }
}

@media screen and(max-width:768px) {
    .about-me{
        width: 100%;
    }
    .credentials{word-break:break-all;}
    .about-me-story {
        width: 100%;
       margin: 0;
        padding: 1rem;
    }
    .cv-buttons{
        display: flex;
        flex-direction: column;
        button{
            font-size:1.5rem;
            padding: 1rem;
        }
    }
}
@media screen and(max-width:768px) {
.about-me-container{
    flex-direction: column;
}
.about-me-left{
   width: 100%;
   height: 100%;
}
.about-me-right{
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding-bottom: 3rem;
   
 }
 .about-me-story {
    width: 100%;
    height: 100%;  
    padding: 1rem;

    h1,
    h2 {
        font-size: 3rem;
    }
    p{
        padding: 2rem 0;
    }
}
.cv-buttons{
   background-color: transparent;
  align-items: center;
    button{
        width: 60%;
        font-size:1.9rem;
        padding: 1rem;
    }
}
}