.contact{
    position: absolute;
    height: 100vh;
    top: 0;
   width: 100%;
    background-color: $green;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.contact-container{
    padding-top: 10rem;
    margin: 0 auto;
    width: 80%;
    background-color: $green;
}
.contact-container.social-media{
    width: 100%;
    padding-top: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: $green;
}


.button {
    text-transform: uppercase;
    background-color: black;
    color: white;
    outline: none;
    border: none;
    font-size: 1.8rem;
    font-family: "Fjalla One", sans-serif;
 
    padding: 1.5rem;

    &:active,
   &:visited {
        color: white;
    }
    &:hover {
        color: red;
    }
}

.contact-form {
    margin: 0 auto;
    width: 70%;

}
.form-text{
    font-size: 2.5rem;
    padding-bottom: 3rem;
}

.form-input,
.text-area {
    width: 100%;
    height: 7rem;
    padding: 0 3rem;
    font-size: 2.4rem;
    border: none;
    box-shadow: 0 0 5px #333;
    font-family: 'Montserrat';
    margin-bottom: 4rem;
    font-weight: 300;
    cursor: pointer;
}
.text-area {
    height: 16rem !important;
    resize: none;
    padding: 3rem !important;
}
.media-icons{
    width: 10%;
    background-color: red;
    display:flex;
    justify-content: space-evenly;
    font-size: 3rem;
}
.message-sent{

    font-size: 4rem;
}
@media screen and(max-width:1024px){
    .contact-container.social-media,
    .contact-container{
        width: 100%;
    }
    .contact-form {
        width: 90%;
    }
    .media-icons{
        width: 15%;
    }
    .form-text{
        padding-top: 6rem;
    }
    
}
@media screen and(max-width:768px){
    
    .media-icons{
        width:20%;
    }
}
@media screen and(max-width:425px){
    .contact-container{
        padding-top: 2rem;
    }
    .contact-container.social-media,
    {
       padding-top: 1rem;
       h3{font-size: 2rem;}
    }
    .media-icons{
        width:30%;
       margin-bottom: 1rem;
    }
    .form-input{
        margin-bottom: 1rem;
    }
    .text-area {
        height: 8rem !important;
     
        padding: 3rem !important;
        margin-bottom: 1rem;
    }
    .form-text{
       
        font-size: 2rem;
    }
}